import React, { Component } from 'react';
import Mlbtable from './Mlbtable';
import Like from '../components/common/like';

//         
// (        DOM  )
export default class PlayerTable extends Component {
  columns = [
    //  : 
    { key:'nu',path: 'nu', label: 'No', class:'no' },
    { key:'po', path: 'po', label: 'Pos', class:'pos' },
    { key:'starters', path: 'starters', label: 'Starters', class:'starters' },
    { key:'nm',  path: 'nm', label: 'Name', class:'name',  
      content: movie => <p><em liked={movie.nm} onClick={() => this.props.onLike(movie)}></em>{movie.nm}</p> 
    },
    { path: 'fa', label: 'FPPG', class:'fppg' },
    { key:'pir', path: 'pir', label: 'Pick %', class:'pick',
      content: movie => <p>{Math.round(movie.pir * 100) / 100}</p>
    },    
    // Like  Delete Like  Button    content  JavaScript ES6  
    // {
    //   key: 'like',
    //   content: movie => <Like liked={movie.liked} onClick={() => this.props.onLike(movie)} />
    // },
    {
      key: 'select',
      //content: movie => <button onClick={() => this.props.onDelete(movie)} className="btn btn-danger btn-sm">Delete</button>
      content: movie => 
      <button onClick={() => this.props.onDelete(movie)} className="btn-plus-minus" type="button">
        <span className="icon-wrapper">
          <span className="icon-bar"></span>
          <span className="icon-bar"></span>													
        </span>                        
      </button>	
    },
  ]

  render() {
    const { movies, onSort, sortColumn } = this.props;

    return (
      <Mlbtable
        columns={this.columns}
        data={movies}
        sortColumn={sortColumn}
        onSort={onSort}
      />
      
    );
  }
}