    //     <li
    //       key={item[valueProperty]} // item[valueProperty] item._id 
    //       onClick={() => onItemSelect(item)}
    //       className={item === selectedItem ? "list-group-item active" : "list-group-item"}
    //     >
    //       {item[textProperty]} {/* item[textProperty] item.name  */}
    //     </li>)}
    // </ul>
