import React from 'react';

const SelectTeamTab = (props) => {
  const {
    items, //  
    textProperty, //  name
    valueProperty, //  _id
    selectedItem, //  
    onItemSelect, //    
    tabColor
  } = props;
  
  //console.log(tabColor);
  return (       
    <div className="select-team">       
        {items.map((item ,index )=>        
        <div          
          key={item[valueProperty]} // item[valueProperty] item._id           
          index={index}
          onClick={() => onItemSelect(item, index)}
          className={tabColor === index ? "team active" : "team"}
        >          
          {item[textProperty]}           
        </div>)}       
    </div>      
  );
}

// Default Props: Props      
SelectTeamTab.defaultProps = {
  textProperty: "name",
  valueProperty: "_id"
}

export default SelectTeamTab;