import React from "react";
import axios from "axios";
import ScrollArea from 'react-scrollbar';
import * as utils from '../../common/utils';
import SelectTeam from "./SelectTeam";
import SelectPlayer from "./SelectPlayer";
import Movies from '././../components/movies';
class Condition extends React.Component {
  state = {          
    playerlist : [],                 
    gameid: this.props.settinginfo.gi,
    gametype: this.props.settinginfo.gt,
    homeabbr: this.props.settinginfo.hta,
    awayabbr: this.props.settinginfo.ata,
    hometeam: this.props.settinginfo.htn,
    awayteam: this.props.settinginfo.atn,
    hti: this.props.settinginfo.hti,
    ati: this.props.settinginfo.ati
  };
  getTeamStat= async (gametype, gameid) =>{
    const {data : {result: teamStat }} = await axios.get(utils.DATA_URL+`/stat/team/${gametype}/${gameid}`);    
    this.year = new Date().getFullYear();
    const nowYear = String(this.year);    
      {Object.keys(teamStat).map((key ,index) => {        
        if(key ==="stat"){                   
          {teamStat[key].map((statlist, index) => {                
            if(statlist.season_year=== nowYear){
              if(statlist.opponent_team_id == "ALL"){                                  
                this.setState({                    
                  teamStat: statlist,                   
                });                                
              }                
            }                         
          })}                      
        }                
      })}                  
  };
  
  componentDidMount(){
    this.getTeamStat(this.props.settinginfo.gt, this.props.settinginfo.gi);     
  }; 
  render() {        
    const {playerlist, gameid, gametype, homeabbr, awayabbr, hometeam, awayteam} = this.state;    
    //console.log(playerlist);

    return(    
        <div className="condition-content">         
            <SelectTeam {...this.props.settinginfo} teamStat={this.state.teamStat} teamStatH={this.state.teamStatH} teamStatA={this.state.teamStatA} />             
            <SelectPlayer {...this.props.settinginfo} />

            <ul className="txt-box">
              <li>A 10% commission will be collected from your Entry Fee by RankingBall.</li>
              <li>User-created Contests will be refunded and converted to Free Contest unless three or more players enter.(TOURNAMENT, Head-to-Head)</li>
              <li>User-created Contests cannot be canceled(and refunded) by the user.</li>
              <li>Submitted Entries can be modified in the Upcoming tab up to 5 minutes before the real game starts.</li>             
            </ul>                           
        </div>
    )
  }
};
export default Condition;