import React, { Component } from 'react';
import SelectTeamTab from './SelectTeamTab';
import Pagination from '../components/common/pagination';
//import { paginate } from '../../common/utils';
import PlayerTable from './PlayerTable';
import _ from 'lodash';

export default class SelectPlayer extends Component {
  state = {
    gameid: this.props.gi,
    gametype: this.props.gt,
    homeabbr: this.props.hta,
    awayabbr: this.props.ata,
    hometeam: this.props.htn,
    awayteam: this.props.atn,
    
    tabColor: 0,
    
    movies: [],
    genres: [],
    currentPage: 1, //   
    pageSize: 100, //     
    sortColumn: { path: 'title', order: 'asc' }
  }  
  componentDidMount() {
    //const genres = [{ _id: "", name: 'All Genres' }, ...getGenres()];     
    if(this.props.gt ===104002){
        var away = this.props.ata
        var home = this.props.hta
    }else{
        var away = this.props.atn
        var home = this.props.htn
    }    

    const genres = [
        { _id: this.props.ati, name: away},
        { _id: this.props.hti, name: home},
      ]
    
     
    //     , All Genres _id    ListGroup  map()    key       .
    //this.setState({ movies: getMovies(), genres });    
    //console.log(this.props.pl);
    this.setState({ movies: this.props.pl, genres });    
    
  }

  handleDelete = (movie) => {
    //const movies = this.state.movies.filter(m => m.id !== movie.id);
    //this.setState({ movies: movies });
    // this.setState({ movies }); //   . Modern JavaScript   value     .
  }

  handleLike = (movie) => {
    const movies = [...this.state.movies];
    const index = movies.indexOf(movie);
    movies[index] = { ...movies[index] }; //   ...?
    movies[index].liked = !movies[index].liked;
    console.log(movies[index].liked);
    this.setState({ movies });
  }

  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  }

  handleGenreSelect = (genre, index) => { 
    this.setState({ selectedGenre: genre, tabColor:index, currentPage: 1 }); //    1  2      
  }

  handleSort = (sortColumn) => {
    //console.log(sortColumn);
    this.setState({ sortColumn });
  }
  //      
  getPagedData = () => {
    const {
      //pageSize,
      //currentPage,
      sortColumn,
      selectedGenre,
      movies: allMovies
    } = this.state;

    //console.log(allMovies);
    //      
    // 1. selectedGenre  _id     All Genres   ?  
    // 2. All Genres _id (: "") !      
      
    const filtered = selectedGenre && selectedGenre._id
      ? allMovies.filter(m => m.ti === selectedGenre._id)
      : allMovies.filter(m => m.ti === this.props.ati);                
    
    // orderBy() :       
    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);
    
    if(sortColumn.path==="nu"){
      if(sortColumn.order==="desc"){
        const result = sorted.slice().sort((a,b) => b.nu.localeCompare(a.nu, undefined, {numeric: true}));    
        return { totalCount: filtered.length, data: result };        
      }else{
        const result = sorted.slice().sort((a,b) => a.nu.localeCompare(b.nu, undefined, {numeric: true}));    
        return { totalCount: filtered.length, data: result };        
      }            
    }else{
      return { totalCount: filtered.length, data: sorted };        
    }            
    // allMovies  filtered 
    //const movies = paginate(sorted, currentPage, pageSize);
    //console.log(filtered.length);    
  }

  render() {
    const { length: count } = this.state.movies;
    const { pageSize, currentPage, sortColumn } = this.state;
    
    if (count === 0) return <p>There are no movies in the database.</p>
    //console.log(movies);
    const { totalCount, data: movies } = this.getPagedData(); // State     

    return (
      /* className row()  col() div : Bootstrap Grid System */
      <div className="select-content">
            <p className="select-tit">Select Batters</p>          
            <SelectTeamTab
                items={this.state.genres}
                selectedItem={this.state.selectedGenre}
                tabColor={this.state.tabColor}
                onItemSelect={this.handleGenreSelect}
            />
            <p className="select-txt">At least one player from both teams must be selected.</p>            
            <PlayerTable
                movies={movies}
                sortColumn={sortColumn}
                onLike={this.handleLike}
                onDelete={this.handleDelete}
                onSort={this.handleSort}
            />
            <Pagination
              itemsCount={totalCount}
              pageSize={pageSize}
              currentPage={currentPage} //  
              onPageChange={this.handlePageChange}
            />            
      </div>            
    )
  }
}
