import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretUp, faCaretDown } from '@fortawesome/free-solid-svg-icons'
// columns: array
// sortColumn: object
// onSort: function

export default class TableHeader extends Component {
  //       
  //         
  raiseSort = path => {
    const sortColumn = { ...this.props.sortColumn };
   
    if (sortColumn.path === path) //     
      sortColumn.order = (sortColumn.order === "asc") ? "desc" : "asc";
    else { //      
      sortColumn.path = path;
      sortColumn.order = "asc";
    }

    this.props.onSort(sortColumn);
  };

  // column         
  //    https://fontawesome.com/v4.7.0/icon/sort-asc 
  renderSortIcon = (column) => {
    const { sortColumn } = this.props;

    if (column.path !== sortColumn.path) return null;

     //if (sortColumn.order === 'asc') return <i className="fa fa-sort-asc" />
    if (sortColumn.order === 'asc') return  <em><FontAwesomeIcon icon={faCaretUp} /></em>    
    return <em><FontAwesomeIcon icon={faCaretDown} /></em>    
  }

  render() {
    return (
      <div className="tb-tit">      
          {this.props.columns.map(column => (
            <span
              key={column.path || column.key} // column.path     column.key  
              className={column.class}
              onClick={() => this.raiseSort(column.path)}
            >
             {column.label} {this.renderSortIcon(column)}
            </span>
          ))}        
      </div>
    );
  }
}