import React, { Component } from 'react';
import ScrollArea from 'react-scrollbar';
import _ from 'lodash';

export default class TableBody extends Component {
  // column content   content     item(movie)  
  renderCell = (item, column) => {
    if (column.content) {
      return column.content(item);
    }

    // column content   item(movie)  column   
    return _.get(item, column.path); // lodash get(obejct, path) : object path  
  }

  // map()    Unique keys   
  createKey = (item, column) => {
    return item._id + (column.path || column.key); // column path  key          
  }

  render() {
    const { data, columns } = this.props;
    //console.log(this.props);
    return (
      <ul className="tb-txt">
        <ScrollArea className="playlist" smoothScrolling={true} horizontal={false}>
        {/* :   (data)    (item)    */}
        {data.map(item => (
          item.po === "P" ? null :(
            <li key={item.id} >
            {/* :   (item)  (column)    */} 
            {columns.map(column =>
              <span className={column.class} key={this.createKey(item, column)}>{this.renderCell(item, column)}</span>
            )}
            </li>
          )                    
        ))}
        </ScrollArea>
      </ul>
    );
  }
}