import React from "react";
//import * as utils from '../../common/utils';
class SelectTeam extends React.Component {
  state = {                   
    gameid: this.props.gi,
    gametype: this.props.gt,
    homeabbr: this.props.hta,
    awayabbr: this.props.ata,
    hometeam: this.props.htn,
    awayteam: this.props.atn, 
    hpn: this.props.hpn,
    apn: this.props.apn,
    hps: this.props.hps,
    aps: this.props.aps,
    hpe: Math.round(this.props.hpe * 100) / 100,
    ape: Math.round(this.props.ape * 100) / 100
  };    
  selectTeam(id){           
      this.setState({tabSubActive: id});      
  };
  componentDidUpdate(prevProps){       
      //console.log(this.props); 
    if(this.props !== prevProps) {      
        if(this.props.gt===104002){                        
            if(this.props.teamStat.team_id === this.props.ati){
                this.setState({                    
                    apitw : this.props.teamStat.p_win,                    
                    aloss : this.props.teamStat.p_lose,                    
                    adraw : this.props.teamStat.h_d,                    
                    apitSv : this.props.teamStat.p_save,                           
                    apitEra : Math.round(this.props.teamStat.p_era * 100) / 100
                })
            }else{
                this.setState({                    
                    hpitw : this.props.teamStat.p_win,                    
                    hloss : this.props.teamStat.p_lose,                    
                    hdraw : this.props.teamStat.h_d,                    
                    hpitSv : this.props.teamStat.p_save,                           
                    hpitEra : Math.round(this.props.teamStat.p_era * 100) / 100
                })
            }                        
        }else{
            if(this.props.teamStat.team_id === this.props.ati){
                this.setState({                                         
                    apitw : this.props.teamStat.pitW,                     
                    aloss : this.props.teamStat.loss,                     
                    adraw : this.props.teamStat.draw,                     
                    apitSv : this.props.teamStat.pitSv,                     
                    apitEra : this.props.teamStat.pitEra 
                })
            }else{
                this.setState({                    
                    hpitw : this.props.teamStat.pitW,                     
                    hloss : this.props.teamStat.loss,                     
                    hdraw : this.props.teamStat.draw,                     
                    hpitSv : this.props.teamStat.pitSv,                     
                    hpitEra : this.props.teamStat.pitEra 
                })
            }   
        }                
    }
  } 
  render() {        
    const {gametype, homeabbr, awayabbr, hometeam, awayteam, hpn, apn, hps, aps, hpe, ape, hpitw, apitw, hloss, aloss, hdraw, adraw, hpitSv, apitSv, hpitEra, apitEra} = this.state;        
    //console.log(this.props);
    return(                        
        <div className="select-team-box">
            <p className="tit">Select Pitching Team</p>
            <p className="icon-exc"></p>            
            {gametype===104002 && (//mlb
            <div className="select-team-btn">
                <div className={this.state.tabSubActive === 0 ? "selectTeam awayTeam active" : "selectTeam awayTeam"} onClick={() => this.selectTeam(0)}>
                    <em className={"t-logo logo-MLB-"+awayabbr}></em>
                    <span data-id="">{awayabbr}</span>
                </div>
                <div className={this.state.tabSubActive === 1 ? "selectTeam homeTeam active" : "selectTeam homeTeam"} onClick={() => this.selectTeam(1)}>
                    <span data-id="">{homeabbr}</span>
                    <em className={"t-logo logo-MLB-"+homeabbr}></em>
                </div>		
            </div>
            )}
            {gametype===104012 && (//kbo
            <div className="select-team-btn">                
                <div className={this.state.tabSubActive === 0 ? "selectTeam awayTeam active" : "selectTeam awayTeam"} onClick={() => this.selectTeam(0)}>
                    <em className={"t-logo logo-KBO-"+awayabbr}></em>
                    <span data-id="">{awayteam}</span>
                </div>
                <div className={this.state.tabSubActive === 1 ? "selectTeam homeTeam active" : "selectTeam homeTeam"} onClick={() => this.selectTeam(1)}>
                    <span data-id="">{hometeam}</span>
                    <em className={"t-logo logo-KBO-"+homeabbr}></em>
                </div>		
            </div>
            )}            
            <div className="select-team-infor">
                <div className="first">						
                    <div className="sr-txt">
                        <span><strong>W</strong></span>
                        <span>{apitw}</span>
                        <span><strong>L</strong></span>
                        <span>{aloss}</span>
                        <span><strong>D</strong></span>
                        <span>{adraw}</span>
                        <span><strong>S</strong></span>
                        <span>{apitSv}</span>
                        <span><strong>ERA</strong></span>
                        <span>{apitEra}</span>
                    </div>
                    <div id="away-sp-txt" className="sp-txt" >
                        <span><strong>SP</strong></span>
                        <span id="spName">{apn}</span>
                        <span id="spStat">({aps}, {ape})</span>
                    </div>
                </div>
                <div className="middle">
                    <span>AWAY</span>
                    <span>HOME</span>
                </div>				
                <div className="second">					
                    <div className="sr-txt">
                        <span><strong>W</strong></span>
                        <span>{hpitw}</span>
                        <span><strong>L</strong></span>
                        <span>{hloss}</span>
                        <span><strong>D</strong></span>
                        <span>{hdraw}</span>
                        <span><strong>S</strong></span>
                        <span>{hpitSv}</span>
                        <span><strong>ERA</strong></span>
                        <span>{hpitEra}</span>
                    </div>
                    <div id="home-sp-txt" className="sp-txt">
                        <span><strong>SP</strong></span>                        
                        <span id="spName">{hpn}</span>
                        <span id="spStat">({hps}, {hpe})</span>
                    </div>
                </div>
            </div>
            <p className="txt">Winning and QS points are given to the selected pitching team according to the results of the competition.</p>
        </div>                                       
    )
  }
};
export default SelectTeam;